* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #000;
  color: #fff;
  font-family: var(--font-geist-sans);
}

:root {
  --font-geist-sans: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  --bg-gradient: linear-gradient(135deg, #020205 0%, #050510 50%, #000000 100%);

  /* Glow Variables - Starship Theme */
  --glow-teal: 0 0 10px rgba(0, 245, 255, 0.4), 0 0 20px rgba(0, 245, 255, 0.2);
  --glow-cyan: 0 0 10px rgba(6, 182, 212, 0.4), 0 0 20px rgba(6, 182, 212, 0.2);
  --glow-magenta: 0 0 10px rgba(217, 70, 239, 0.4), 0 0 20px rgba(217, 70, 239, 0.2);
  --glow-amber: 0 0 10px rgba(251, 191, 36, 0.4), 0 0 20px rgba(251, 191, 36, 0.2);
  --glow-red: 0 0 10px rgba(239, 68, 68, 0.4), 0 0 20px rgba(239, 68, 68, 0.2);
}

.dashboard-container {
  background: var(--bg-gradient);
  background-attachment: fixed;
  position: relative;
  overflow-x: hidden;
}

/* Parallax Starfield */
.dashboard-container::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:
    radial-gradient(1px 1px at 10% 10%, rgba(255, 255, 255, 0.8), transparent),
    radial-gradient(1px 1px at 20% 20%, rgba(255, 255, 255, 0.6), transparent),
    radial-gradient(2px 2px at 30% 30%, rgba(255, 255, 255, 0.8), transparent),
    radial-gradient(1px 1px at 40% 40%, rgba(255, 255, 255, 0.6), transparent),
    radial-gradient(1px 1px at 50% 50%, rgba(255, 255, 255, 0.8), transparent),
    radial-gradient(2px 2px at 60% 60%, rgba(255, 255, 255, 0.6), transparent),
    radial-gradient(1px 1px at 70% 70%, rgba(255, 255, 255, 0.8), transparent),
    radial-gradient(1px 1px at 80% 80%, rgba(255, 255, 255, 0.6), transparent),
    radial-gradient(2px 2px at 90% 90%, rgba(255, 255, 255, 0.8), transparent);
  background-size: 550px 550px;
  opacity: 0.6;
  z-index: 0;
  pointer-events: none;
}

/* Cyber Grid Overlay */
.dashboard-container::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 200%;
  background-image:
    linear-gradient(rgba(0, 245, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 245, 255, 0.03) 1px, transparent 1px);
  background-size: 40px 40px;
  transform: perspective(500px) rotateX(60deg) translateY(-100px) translateZ(-200px);
  opacity: 0.15;
  z-index: 0;
  pointer-events: none;
  animation: grid-move 60s linear infinite;
}

@keyframes grid-move {
  0% {
    transform: perspective(500px) rotateX(60deg) translateY(0) translateZ(-200px);
  }

  100% {
    transform: perspective(500px) rotateX(60deg) translateY(40px) translateZ(-200px);
  }
}

/* Utilities using vars */
.glow-teal {
  box-shadow: var(--glow-teal);
}

.glow-cyan {
  box-shadow: var(--glow-cyan);
}

.glow-magenta {
  box-shadow: var(--glow-magenta);
}

.glow-amber {
  box-shadow: var(--glow-amber);
}

.glow-red {
  box-shadow: var(--glow-red);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes glow {

  0%,
  100% {
    box-shadow: 0 0 5px rgba(0, 255, 255, 0.5);
  }

  50% {
    box-shadow: 0 0 20px rgba(0, 255, 255, 0.8);
  }
}

@keyframes flicker {

  0%,
  100% {
    opacity: 1;
    text-shadow: var(--glow-cyan);
  }

  50% {
    opacity: 0.95;
    text-shadow: none;
  }
}

@keyframes flicker-teal {

  0%,
  100% {
    opacity: 1;
    text-shadow: var(--glow-teal);
  }

  50% {
    opacity: 0.95;
    text-shadow: none;
  }
}

@keyframes flicker-magenta {

  0%,
  100% {
    opacity: 1;
    text-shadow: var(--glow-magenta);
  }

  50% {
    opacity: 0.95;
    text-shadow: none;
  }
}

.tab-active-teal {
  animation: flicker-teal 2s infinite;
}

.tab-active-cyan {
  animation: flicker 2s infinite;
}

.tab-active-magenta {
  animation: flicker-magenta 2s infinite 0.5s;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }

  100% {
    background-position: 200% 0;
  }
}

@keyframes lift-burst {
  0% {
    transform: scale(1) translateY(0);
    box-shadow: var(--glow-teal);
  }

  50% {
    transform: scale(1.08) translateY(-4px);
    box-shadow: 0 20px 40px rgba(0, 245, 255, 0.6);
  }

  100% {
    transform: scale(1.05) translateY(-2px);
    box-shadow: var(--glow-teal);
  }
}

@keyframes pulse-slow {

  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.8;
  }
}

/* Sticky hierarchy */
.sticky-hero {
  position: sticky;
  top: 0;
  z-index: 30;
}

.motion-safe\:transition-none {
  transition: none !important;
}

/* Animation Utilities */
.animate-shimmer {
  animation: shimmer 2s infinite linear;
}

.animate-pulse-slow {
  animation: pulse-slow 3s infinite ease-in-out;
}

/* Holographic Panel */
.panel-holo {
  background: rgba(0, 20, 30, 0.4);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(0, 245, 255, 0.15);
  box-shadow:
    0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06),
    inset 0 0 20px rgba(0, 245, 255, 0.05);
  position: relative;
  overflow: hidden;
}

/* Scanline Effect */
.panel-holo::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(0, 245, 255, 0.5), transparent);
  animation: scanline 4s linear infinite;
  opacity: 0;
  pointer-events: none;
}

.panel-holo:hover::after {
  opacity: 0.5;
}

@keyframes scanline {
  0% {
    top: 0%;
    opacity: 0;
  }

  10% {
    opacity: 0.8;
  }

  90% {
    opacity: 0.8;
  }

  100% {
    top: 100%;
    opacity: 0;
  }
}

